import React, { useContext } from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';
import IsLoading from '../IsLoading/IsLoading';
import { Link } from 'react-router-dom';
import { CartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';
import { NavbarContext } from '../../Context/NavbarContext';
const FeaturedProducts = () => {

    function featuredProducts() {
        return axios.get('https://ecommerce.routemisr.com/api/v1/products')
    }
    let { data, isError, isFetching, isLoading } = useQuery('featuredProducts', featuredProducts)
    // console.log(data?.data.data);
    let { addToCart } = useContext(CartContext)
    let { setNumber } = useContext(NavbarContext)

    async function addProduct(id) {
        let response = await addToCart(id)
        // console.log(response);
        if (response?.data?.status === 'success') {
            // console.log(response.data.numOfCartItems);
            setNumber(response?.data?.numOfCartItems)
            toast.success('product added successfully ', { duration: 4000, className: 'mt-5' })
            localStorage.setItem('cartProblem', response?.data?.numOfCartItems)
        }
    }
    const goTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    };

    return <>
        {isLoading ? <IsLoading /> : <div className="overFlowProblem container text-center py-3">
            <div className="overFlowProblem row justify-content-center">
                {data?.data?.data?.map((product) =>

                    <div key={product._id} className='col-6 col-xl-2 col-lg-2'>
                        <div className='product cursor-pointer p-2'>
                            <Link onClick={goTop} className='text-decoration-none text-black' to={`/productDetails/${product._id}`}>

                                <img className='w-100' src={product.imageCover} alt={product.title} />
                                <span className='text-main font-sm fw-bolder'>{product.category.name}</span>
                                <h5 className='h6 my-2'>{product.title.split(" ").slice(0, 2).join(' ')}</h5>
                                <div className='d-flex justify-content-between mt-3'>
                                    <span>{product.price} EGP</span>
                                    <span> <i className=' fas fa-star rating-color'></i> {product.ratingsAverage}</span>
                                </div>
                            </Link>
                            <button onClick={() => addProduct(product._id)} className='btn bg-main text-white w-100 btn-sm mt-2'>add to cart &nbsp; <i className='fa fa-plus'></i></button>

                        </div>


                    </div>

                )}
            </div>
        </div>}


    </>
}

export default FeaturedProducts;
